.App {
  width: 1000px;
  height: 600px;
  margin: 0 auto;
  padding: 20px;
  background-color: #e0f7fa;
  position: relative;
  overflow: hidden;
  user-select: none;
  border-radius: 16px;
}

.start-title span {
  margin-top: 175px;
  font-family: 'Balatro';
  color: #337eff;
  font-size: 80px;
  text-shadow: 3px 3px 0px rgba(0, 0, 0, 0.2);
}

.start-title span {
  display: inline-block;
  animation: wave 1.5s infinite ease-in-out;
}

.start-button {
  background-color: #337eff;
  margin-top: 30px;
  border-radius: 16px;
  width: 100px;
  padding: 15px;
  color: white;
  font-size: 24px;
  font-family: 'Balatro';
  border: none;
  cursor: pointer;
}

.start-button:hover {
  background-color: #2767d6;
}

.start-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.start-menu p {
  position: absolute;
  bottom: 0px;
  font-family: 'Balatro';
  font-weight: lighter;
  color: #b3b3b3;
}

.specialty-menu {
  text-align: center;
  font-family: 'Balatro';
  font-weight: lighter;
}

.specialty-menu h2 {
  margin-top: 50px;
  font-size: 30px;
  font-weight: lighter;
  color: #696969;
  text-shadow: 2px 2px 0px rgba(82, 82, 82, 0.2);
}

.specialty-buttons {
  display: flex;
  justify-content: center;
  gap: 30px;
  margin-top: 70px;
}

.specialty-button {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: white;
  padding: 30px;
  width: 200px;
  border-radius: 16px;
  border: none;
  cursor: pointer;
  outline: none;
}

.specialty-button img {
  margin-top: -30px;
  margin-bottom: -15px;
}

.cardiology-button {
  animation: wave 1.5s infinite ease-in-out;
  animation-delay: 0s;
}

.respiratory-button {
  scale: 1.05;
  animation: wave 1.5s infinite ease-in-out;
  animation-delay: -0.3s;
  position: relative;
  top: -5px;
}

.specialty-button span {
  bottom: auto;
  font-size: 24px;
  font-family: 'Balatro';
  color: #d85555;
  text-shadow: 2px 2px 0px rgba(82, 82, 82, 0.2);
}

.specialty-button:hover span {
  scale: 1.05;
}

.investigate-container {
  position: absolute;
  width: 100%;
  height: 100%;
  padding-top: 0px;
  top: 0;
  left: 0;
}

.slide-down {
  animation: slideDown 1s ease-out fowards;
}

.slide-down-stem {
  animation: slideDownStem 0.7s ease-out forwards;
}

@keyframes slideDownStem {
  0% {
    transform: translateY(-150%);
    opacity: 1;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes slideDown {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  100% {
    transform: translateY(120%);
    opacity: 1;
  }
}

.patient-stem-container {
  margin: 0 auto;
  width: 800px;
  min-height: 100px;
  text-align: center;
  background-color: white;
  border-radius: 16px;
  margin-bottom: 30px;
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-left: 20px;
  padding-right: 20px;
}

.patient-stem {
  font-size: 17px;
  overflow: hidden;
  color: #333333;
  font-family: 'Balatro';
  font-weight: lighter;
  user-select: none;
  display: inline-block;
  white-space: normal;
  line-height: 2;
  visibility: hidden;
  animation: typing 1s steps(50, end) forwards;
  animation-delay: 0.3s;
}

.patient-image {
  display: block;
  margin: 160px auto 20px; /* Centers the image and adds bottom margin */
  transform: scale(0.75); /* Scales the image similar to the Phaser scaling */
  filter: brightness(110%);
  user-select: none;
}

.patient-image:hover {
  scale: 1.02
}

.popup-message {
  position: absolute;
  top: 410px;
  left: 50%;
  transform: translateX(-50%);
  color: #b50015;
  font-family: 'Balatro';
  font-size: 20px;
  opacity: 0; /* Hidden by default */
  transition: opacity 0.3s ease-in-out; /* Smooth fade-in and fade-out */
}

.popup-message.fade-in {
  opacity: 1; /* Fully visible after fade-in */
}

.popup-message.fade-out {
  opacity: 0; /* Fades out smoothly */
}

.obs-container {
  position: absolute;
  top: 205px;
  left: 240px;
  width: 100px;
  height: auto; /* Accounts for top and bottom padding */
  padding: 20px;
  padding-top: 0px;
  padding-bottom: 5px;
  text-align: left;
  overflow-y: auto;
  font-family: 'Balatro';
  color: #474747;
  background-color: #ffa6002a;
  border-radius: 16px;
}

.investigation-records-container {
  position: absolute;
  top: 210px;
  right: 140px;
  width: 250px;
  height: 200px; /* Accounts for top and bottom padding */
  padding: 20px;
  overflow-y: auto;
  font-family: 'Balatro';
  color: #333333;
}

.investigation-records-container h3 {
  margin-top: 0px;
  font-weight: lighter;
  font-size: 20px;
  text-align: center;
  margin-top: 30px;
}

.highlight-animation {
  animation: pulse 1s forwards;
}

@keyframes pulse {
  0% {
    transform: scale(1);
    color: #333;
  }
  50% {
    transform: scale(1.1);
    color: #00ff33; /* Highlight color */
  }
  100% {
    transform: scale(1);
    color: #333;
  }
}

@keyframes pulse2 {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);

  }
}

@keyframes pulse3 {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.02);
  }
  100% {
    transform: scale(1);

  }
}

.menu-container {
  position: absolute;
  bottom: 20px;
  margin-left: 30px;
  height: 100px;
  display: flex;
  justify-content: center; 
  align-items: center;
  gap: 20px;
}

.menu-button {
  width: 230px;
  height: 100px;
  background-color: #4f90ff;
  color: #ffffff;
  border: none;
  border-radius: 16px;
  cursor: pointer;
  text-align: center;
  font-size: 20px;
  font-family: 'Balatro';
  font-weight: lighter;
  text-shadow: 2px 2px 0px #666666;
}

.menu-button:hover {
  background-color: #337eff;
}

.submenu-container {
  position: absolute;
  bottom: 140px;
  display: flex;
  gap: 20px; 
  justify-content: flex-start; 
  padding-left: 35px; 
  flex-wrap: wrap;
}

.submenu-button {
  padding: 0 20px; 
  height: 50px; 
  background-color: #4f90ff;
  color: #ffffff;
  border: none;
  border-radius: 10px; 
  cursor: pointer;
  text-align: center;
  font-size: 18px;
  font-family: 'Balatro';
  font-weight: lighter;
  text-shadow: 2px 2px 0px #666666;
}

.submenu-button:hover {
  background-color: #337eff;
}

