.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.2); /* Semi-transparent background */
  z-index: 1000; /* Ensure it's above other elements */
}

.modal-content {
  position: absolute;
  display: flex;
  flex-direction: column;
  max-width: 700px;
  width: fit-content;
  background: rgb(255, 255, 255);
  word-wrap: break-word;
  padding: 30px;
  box-sizing: border-box;
  border-radius: 10px;
  text-align: center;
  font-family: 'Balatro';
  box-shadow: 4px 4px 2px rgba(0, 0, 0, 0.1);
}

.modal-box {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 50%;
  left: 50%;
  min-width: 200px;
  width: auto;
  transform: translate(-50%, -50%);
  background: white;
  padding: 30px;
  border-radius: 10px;
  text-align: center;
  font-family: 'Balatro';
  box-shadow: 4px 4px 2px rgba(0, 0, 0, 0.1);
}

.toggle-button {
  scale: 0.8;
  margin-bottom: -25px;
  margin-top: -20px;
  cursor: pointer;
}

.toggle-button:hover {
  transform: scale(1.1);
}

.toggle-button.pulse2 {
  animation: pulse2 1s ease-in-out;
}

.modal-box-content {
  margin-top: -20px;
  margin-bottom: 10px;
  line-height: 1.5;
}

.modal-content h3 {
  font-weight: lighter;
  margin-top: 5px;
  font-size: 24px;
}

.modal-content p {
  overflow: hidden; /* Ensures the content is hidden until the animation reveals it */
  text-align: left;
  display: inline-block;
  white-space: pre-wrap;
  line-height: 1.8;
  margin-left: 4px;
  margin-top: 0px;
  margin-bottom: 20px; /* Adds spacing below the text */
  padding-bottom: 5px;
  animation: 
    typing 0.4s steps(40, end); /* Typing effect */
}

.modal-content p span {
  color: #505050;
  display: inline-block;
  text-align: left;
  line-height: 2.2;
  white-space: pre-wrap;
  animation: typing 0.5s steps(40,end);
  font-size: 15px;
}

.modal-content table {
  margin-bottom: 20px;
  animation: typing 0.4s steps(40, end);
  overflow: hidden;
  display: block;
}

.modal-content table tr {
  line-height: 2;
}

.modal-content table td {
  text-align: left;
  padding: 0px 15px;
}

.modal-content table td:nth-child(3),
.modal-content table td:nth-child(4) {
  color: gray;
}

@keyframes typing {
  from {
    clip-path: inset(0 100% 0 0); /* Hide all text by clipping it */
    visibility: visible; /* Make text visible when animation starts */
  }
  to {
    clip-path: inset(0 0 0 0);
    visibility: visible;
  }
}

.ok-button {
  position: relative;
  margin-left: auto;
  margin-right: -10px;
  margin-bottom: -10px;
  width: 70px;
  padding: 10px 20px;
  font-size: 1em;
  font-family: 'Balatro';
  cursor: pointer;
  background-color: #4f90ff;
  color: white;
  border: none;
  border-radius: 5px;
  text-shadow: 2px 2px 0px #666666;
}

.ok-button:hover {
  background-color: #337eff;
}

.close-button {
  width: 100px;
  position: relative;
  margin-left: auto;
  margin-right: -10px;
  margin-bottom: -10px;
  margin-top: 0px;
  padding: 10px 0px;
  font-size: 1em;
  font-family: 'Balatro';
  cursor: pointer;
  background-color: #4f90ff;
  color: white;
  border: none;
  border-radius: 5px;
  text-shadow: 2px 2px 0px #666666;
}

.proceed-button {
  background-color: #337eff;
  border-radius: 10px;
  font-family: 'Balatro';
  cursor: pointer;
  padding: 10px 20px;
  text-shadow: 2px 2px 0px #666666;
  border: none;
  color: white;
  font-size: 18px;
  animation: pulse2 1s forwards infinite;
}

.diagnosis-container {
  position: absolute;
  width: 100%;
  height: 100%;
  padding-top: 0px;
  top: 0;
  left: 0;
}

.investigation-report-container {
  width: auto;
  height: auto;
  padding: 5px;
  margin: auto;
  margin-top: 165px;
  margin-bottom: -20px;
  border-radius: 16px;
  font-family: 'Balatro';
  color: gray;
}

.investigation-report-container p {
  line-height: 1;
  color: #6b6b6b;
}

.investigation-report-container:has(p:nth-child(5)) {
  font-size: 13px; /* Adjust font size when there are 6 or more items */
}

.diagnosis-question-container {
  position: relative;
  width: 790px;
  height: 270px;
  background-color: white;
  margin: auto;
  margin-top: 30px;
  border-radius: 16px;
  padding: 5px;
  padding-right: 20px;
  font-family: 'Balatro';
  text-align: left;
}

.diagnosis-question-container h3 {
  font-weight: lighter;
  margin-left: 217px;
  padding-left: 2px;
  overflow: hidden;
  line-height: 1.7;
  display: inline-block;
  color: #c80000;
  visibility: hidden;
  animation: typing var(--typing-duration, 1s) steps(50, end) var(--typing-delay, 0.5s) forwards, 
  show 0s var(--typing-delay, 0.5s) forwards;
}

@keyframes show {
  from {
    visibility: hidden;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}

.continue-button {
  position: absolute;
  margin-left: 210px;
  margin-top: 22px;
  padding: 8px 16px;
  font-size: 16px;
  font-family: 'Balatro';
  font-weight: lighter;
  border: none;
  border-radius: 5px;
  background-color: rgba(214, 0, 0, 0.549);
  color: #ffffff;
  cursor: pointer;
  opacity: 0;
  animation: appear 0s linear 0.3s forwards;
}

@keyframes appear {
  to {
      visibility: visible;
      opacity: 1; 
  }
}

.view-explanation-button {
  position: absolute;
  margin-left: 320px;
  margin-top: 22px;
  padding: 8px 16px;
  font-size: 16px;
  font-family: 'Balatro';
  font-weight: lighter;
  border: none;
  border-radius: 5px;
  background-color: white;
  color: #818181;
  cursor: pointer;
  text-decoration: underline;
  opacity: 0;
  animation: appear 0s linear 0.3s forwards;
}

.view-explanation-button:hover {
  scale: 1.05;
}

.continue-button:hover {
  background-color: rgba(172, 0, 0, 0.549);
}

.diagnosis-question-container p {
  position: absolute;
  margin-left: 30px;
  bottom: 0px;
  color: rgba(214, 0, 0, 0.549);
}

.doctor-image {
  scale: 0.45;
  position: absolute;
  left: 5px;
  bottom: -120px;
  filter: brightness(110%);
}

.doctor-image.vibrating {
  animation: vibrating 0.1s linear var(--vibrating-iterations, 10);
}

.vibrating {
  animation: vibrating 0.1s linear 2;
}

@keyframes vibrating {
  0%, 100% {
    transform: translateX(0); /* No movement */
  }
  25% {
    transform: translateX(-3px); /* Move slightly left */
  }
  75% {
    transform: translateX(3px); /* Move slightly right */
  }
}

.answer-grid {
  display: flex;
  position: absolute;
  flex-direction: column;
  gap: 5px;
  top: 95px;
}

.answer-button {
  height: auto;
  color: #555555;
  padding: 10px;
  border: none;
  background-color: white;
  cursor: pointer;
  font-family: 'Balatro';
  font-size: 15px;
  text-align: left;
  margin-left: 210px;
  margin-right: 20px;
  border-radius: 10px;
  width: 550px;
}

.answer-button:hover {
  background-color: rgb(234, 234, 234);
}

.slide-up {
  animation: slideUp 1s ease-out forwards;
}

.slide-up-stem {
  animation: slideUpStem 1s ease-out forwards;
}

@keyframes slideUp {
  0% {
    transform: translateY(100%); /* Start below the screen */
    opacity: 1; /* Invisible at the start */
  }
  100% {
    transform: translateY(0); /* End in normal position */
    opacity: 1; /* Fully visible */
  }
}

@keyframes slideUpStem {
  0% {
    transform: translateY(0); /* Start below the screen */
    opacity: 1; /* Invisible at the start */
  }
  100% {
    transform: translateY(-150%); /* End in normal position */
    opacity: 1; /* Fully visible */
  }
}

.correct-text {
  color: rgb(0, 178, 0);
}

.correct-text span {
  display: inline-block;
  font-size: 30px;
  animation: wave 1.5s infinite ease-in-out;
}

@keyframes wave {
  0%, 100% {
    transform: translateY(0); /* Default position */
  }
  50% {
    transform: translateY(-5px); /* Move up */
  }
}

